@import './imports/global';

.aui-avatar-size(@type, @size) {
    &.aui-avatar-@{type},
    &.aui-avatar-@{type} .aui-avatar-inner {
        height: @size;
        max-width: @size;
        max-height: @size;
        width: @size;
    }
}

.aui-avatar-border-size(@type, @size) {
    @border-radius: (@size * @aui-avatar-border-radius-ratio);

    &.aui-avatar-@{type} {
        border-radius: @border-radius;

        .aui-avatar-inner {
            border-radius: @border-radius;
        }

        &::before {
            border-radius: (@border-radius + @aui-avatar-border-width);
        }
    }
}

// AUI avatar component
.aui-avatar {
    box-sizing: border-box;
    display: inline-block;
    position: relative;
    vertical-align: text-bottom;

    &-inner {
        display: flex;
        justify-content: center;
        align-content: center;

        border-radius: 100%;
        overflow: hidden;
    }

    & img {
        display: block;
        height: auto;
        margin: 0 auto;
        max-height: 100%;
        max-width: 100%;
        width: auto;
    }

    .aui-avatar-size(xsmall, @aui-avatar-size-xsmall);
    .aui-avatar-size(small, @aui-avatar-size-small);
    .aui-avatar-size(medium, @aui-avatar-size-medium);
    .aui-avatar-size(large, @aui-avatar-size-large);
    .aui-avatar-size(xlarge, @aui-avatar-size-xlarge);
    .aui-avatar-size(xxlarge, @aui-avatar-size-xxlarge);
    .aui-avatar-size(xxxlarge, @aui-avatar-size-xxxlarge);

    // The below style aims to minimise any "halo" caused by the antialiasing of the image
    &::before {
        border: @aui-avatar-border-width solid @aui-avatar-border-color;
        border-radius: 100%;
        bottom: 0;
        content: "";
        left: 0;
        margin: -@aui-avatar-border-width;
        position: absolute;
        right: 0;
        top: 0;
    }

    // Project avatars - circular and different sizes
    &-project {
        .aui-avatar-border-size(xxxlarge, @aui-avatar-size-xxxlarge);
        .aui-avatar-border-size(xxlarge, @aui-avatar-size-xxlarge);
        .aui-avatar-border-size(xlarge, @aui-avatar-size-xlarge);
        .aui-avatar-border-size(large, @aui-avatar-size-large);
        .aui-avatar-border-size(medium, @aui-avatar-size-medium);
        .aui-avatar-border-size(small, @aui-avatar-size-small);
        .aui-avatar-border-size(xsmall, @aui-avatar-size-xsmall);
    }
}



